"use client";
import React from "react";
import { toIntegerVND } from "../utils/price";
import InsideCart from "@/assets/icons/inside_cart";

const fmt = (v) => new Intl.NumberFormat("vi-VN", { maximumFractionDigits: 0 }).format(v);

const loadCart = () => {
  if (typeof window === "undefined") return [];
  try {
    const raw = localStorage.getItem("cart_items");
    const parsed = JSON.parse(raw || "[]");
    return Array.isArray(parsed) ? parsed : [];
  } catch {
    return [];
  }
};

const saveCart = (items) => {
  try {
    localStorage.setItem("cart_items", JSON.stringify(items));
  } catch {}
};

export default function Page() {
  const [items, setItems] = React.useState([]);

  React.useEffect(() => {
    setItems(loadCart());
  }, []);

  const setAndSave = (updater) => {
    setItems((prev) => {
      const next = typeof updater === "function" ? updater(prev) : updater;
      saveCart(next);
      return next;
    });
  };

  const toggleAll = (checked) => {
    setAndSave(items.map((it) => ({ ...it, selected: !!checked })));
  };

  const toggleOne = (id, checked) => {
    setAndSave(items.map((it) => (String(it.product_id) === String(id) ? { ...it, selected: !!checked } : it)));
  };

  const removeOne = (id) => {
    setAndSave(items.filter((it) => String(it.product_id) !== String(id)));
  };

  const clearAll = () => setAndSave([]);

  const changeQty = (id, delta) => {
    setAndSave(
      items.map((it) => {
        if (String(it.product_id) !== String(id)) return it;
        const next = Math.max(1, (Number(it.quantity) || 1) + delta);
        return { ...it, quantity: next };
      })
    );
  };

  const priceParts = (it) => {
    const price = toIntegerVND(it.price);
    const discount = toIntegerVND(it.discount_price);
    const has = discount > 0 && discount < price;
    const final = has ? Math.max(price - discount, 0) : price;
    return { price, discount: has ? discount : 0, final };
  };

  const selectedItems = items.filter((it) => it.selected);
  const subtotal = selectedItems.reduce((sum, it) => sum + priceParts(it).price * (Number(it.quantity) || 1), 0);
  const discountTotal = selectedItems.reduce((sum, it) => sum + priceParts(it).discount * (Number(it.quantity) || 1), 0);
  const total = Math.max(subtotal - discountTotal, 0);

  const allChecked = items.length > 0 && items.every((it) => it.selected);

  if (items.length === 0) {
    return (
      <div className="w-full flex justify-center mt-6 mb-12">
        <div className="w-[90%] max-w-[1200px]">
          <div className="bg-[#ECECEC] rounded-xl border p-10 flex items-center justify-center h-[420px]">
            <div className="text-center text-gray-500">
              <div className="mx-auto mb-4 w-24 h-24 bg-gray-300 rounded-full" />
              <div className="text-lg">Giỏ hàng hiện đang trống</div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="w-full flex justify-center mt-6 mb-12">
      <div className="w-[90%] max-w-[1200px]">
        <div className="bg-[#ECECEC] rounded-xl border p-6">
          <div className="text-center text-2xl font-semibold mb-6">Giỏ hàng</div>
          <div className="flex items-center justify-between mb-4">
            <label className="flex items-center gap-2 text-sm">
              <input
                type="checkbox"
                checked={allChecked}
                onChange={(e) => toggleAll(e.target.checked)}
              />
              Tất cả sản phẩm
            </label>
            <button className="text-sm text-red-500" onClick={clearAll}>Xóa tất cả</button>
          </div>

          <div className="divide-y bg-white rounded-lg border">
            {items.map((it) => {
              const { price, final } = priceParts(it);
              return (
                <div key={it.product_id} className="p-4 flex items-center gap-4">
                  <input
                    type="checkbox"
                    checked={!!it.selected}
                    onChange={(e) => toggleOne(it.product_id, e.target.checked)}
                  />
                  {it.image_url ? (
                    <img src={it.image_url} alt={it.name} className="w-[64px] h-[64px] object-cover rounded" />
                  ) : (
                    <div className="w-[64px] h-[64px] bg-gray-200 rounded" />
                  )}
                  <div className="flex-1">
                    <div className="text-[16px]">{it.name}</div>
                    <div className="mt-2 inline-flex items-center border rounded">
                      <button className="px-3 py-1" onClick={() => changeQty(it.product_id, -1)}>-</button>
                      <div className="px-4 py-1 border-l border-r select-none">{Number(it.quantity) || 1}</div>
                      <button className="px-3 py-1" onClick={() => changeQty(it.product_id, 1)}>+</button>
                    </div>
                    <div className="mt-2 text-[#3A3A3A] font-semibold">{fmt(final)} đ</div>
                  </div>
                  <button
                    className="text-gray-400 hover:text-red-500"
                    title="Xóa"
                    onClick={() => removeOne(it.product_id)}
                  >
                    <InsideCart/>
                  </button>
                </div>
              );
            })}
          </div>

          <div className="mt-6 grid grid-cols-1 md:grid-cols-3">
            <div className="md:col-start-3 md:col-span-1 md:justify-self-end w-full md:w-[300px]">
              <div className="text-sm text-gray-700 flex justify-between"><span>Tạm tính</span><span>{fmt(subtotal)} đ</span></div>
              <div className="text-sm text-gray-700 flex justify-between"><span>Giảm giá</span><span>{fmt(discountTotal)} đ</span></div>
              <div className="text-base font-semibold flex justify-between mt-1"><span>Tổng tiền</span><span>{fmt(total)} đ</span></div>
              <button className="mt-4 w-full h-[44px] rounded-full bg-[#9B8D6F] text-white">Tiếp tục</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
